.FrontProfile {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.FrontAbout {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.FrontProjects {
    display: flex;
}

.FrontFooter {
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.FrontCv {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}


.NavBar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    box-shadow: 0 6px 12px 0 rgba(0,0,0,0.2);
    position: sticky;
    width: 100%;
    z-index: 11;
    top: 0;
}

.NavButtons{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.logo {
    display: flex;
    font-size: 3vh;
    margin: 2vh;
    width: 6vh;
    height: 6vh;
    align-items: start;
}

.ProfileButton {
    width: 11vh;
    height: 5vh;
    font-size: 3vh;
    border: 0px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
}

.ProjectButton {
    width: 13vh;
    height: 5vh;
    font-size: 3vh;
    border: 0px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
}

.CvButton {
    width: 11vh;
    height: 5vh;
    margin-right: 3vh;
    font-size: 3vh;
    border: 0px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
}

@media only screen and (max-width: 480px) {
    .FrontProfile {height: 800px;}
    .FrontAbout {height: 1300px;}
    }