.AboutView{
    display: flex;
    flex-direction: row;
}

.AboutViewTexts {
    display: flex;
    flex-direction: column;
    margin: 2vh;
    padding: 3vh;
    box-shadow: 0 6px 12px 0 rgba(0,0,0,0.2);
    border-radius: 15px;
    max-width: 50vh;
}

.words{
    display: flex;
    flex-direction: row;
    color: #1c73c4;
}

.AboutViewAbout {
    font-size: 5vh;
    display: flex;
    align-self: center;
}

h3 {
    font-size: 2vh;
    margin: 3vh;
}

.AboutPictureOfMe {
    width: 37.7vh;
    height: 50vh;
    margin: auto;
    box-shadow: 0 6px 12px 0 rgba(0,0,0,0.2);
    border-radius: 15px;
}

.PictureOfMe {
    width: 37.7vh;
    height: 50vh;
    border-radius: 15px;
}

.AboutInfo {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 50vh;
}

.abouttext {
    width: 100vh;
}

@media only screen and (max-width: 480px) {
    .AboutView { flex-direction: column;}
    .AboutViewTexts { max-width: 320px;}
    .AboutInfo {width: auto;}
}