@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

.head {
    align-self: center;
}

.ProjectsAll {
    display: flex;
    flex-direction: row;
}

.ProjectImage{
    width: 40vh !important;
    height: 70vh !important;
}

.EachProject {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 10vh;
}

.DataTexts {
    width: 40vh;
    padding: 4vh;
    display: flex;
    margin: 5vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.AllProjects {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
}

.ProjectsText {
    font-size: 1.5vh;
}

.GithubLink {
    font-size: 2vh;
}

.carousel-container {
    width: 40vh;
    margin: 0 auto;
}

.carousel-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70vh;
    width: 35.6vh;
}

.carousel-slide img {
    width: 100vh;
    height: 100vh;
    object-fit: contain;
}

.slick-prev::before, .slick-next::before {
    font-size: 4vh;
    color: #24A6C2;
    z-index: 1;
}

.slick-prev {
    left: -4vh;
}

.slick-dots li button:before {
    font-size: 1vh;
    color: #24A6C2;
}

.slick-dots li.slick-active button:before {
    color: #1c73c4;
}

.slick-prev, .slick-next {
    z-index: 1;
}

@media only screen and (max-width: 480px) {
    .EachProject { flex-direction: column; margin: 0px; margin-top: 10vh; margin-bottom: 10vh;}
    .DataTexts {margin: 0px;}
    .slick-prev {left: -10px;}
    .slick-next {right: 12px;}
    .slick-prev::before, .slick-next::before {
        font-size: 6vh;
    }
}